.Header {
    position: fixed;
    top: 0;
    width: 220px;
    height: 100%;
    background: #f9f9f9;
}

.Header h1 {
    position: relative;
    height: 20%;
    width: 180px;
    margin: 50px 0 0 10px;
}

.Header ul {
    padding: 0 40px 0 0;
    text-align: right;
    font-size: 16px;
    font-weight: 500;
}

.Header ul li {
    margin: 0 0 10px 0;
}

.Header .snb {
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: 14px;
}

.FullPage {
    position: relative;
}

.mainimg {
    padding: 0 0 0 220px;
    width: 100%;
    height: 100vh;
}

.full-page-controls {
    display: none;
}

.Main_product {
    position: relative;
    height: auto;
}

.Main_product ul {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    padding: 0 0 0 220px;
}

.Main_product li {
    position: relative;
}

.Main_product li img {
    width: 100%;
    height: 400px;
    object-fit: cover;
}

.Main_product li .dec {
    position: absolute;
    bottom: 0;

    font-size: 15px;
}

.fp-responsive .fp-auto-height-responsive .fp-section,
.fp-responsive .fp-auto-height-responsive .fp-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
    height: auto !important;
}

.Footer {
    height: 100vh;
}